.page {
  background-color: #fff2d8;
  font-family: 'Nunito', sans-serif;
  min-width: 320px;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  font-weight: 400;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
}


.header {
  border-bottom: 1px solid rgb(84, 84, 84, 0.7);
  width: 100%;
  display: flex;
  justify-content: center;
  gap: 70px;
  height: 70px;
  align-items: center;
}

.header__link {
  text-decoration: none;
  color: black;
  cursor: pointer;
}

.header__links {
  display: flex;
  gap: 170px;
}

.header__link_icon{
  width: 50px;
  height: 50px;
  position: absolute;
  right: 0;
  margin-right: 50px;
  top:0;
  margin-top: 10px;
}


.greetings {
    margin-top: 20px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    gap: 270px;
    border-bottom: 1px solid rgb(84, 84, 84, 0.7);
}
.greetings__about{
   display: flex;
    margin-top: 100px;
   flex-direction: column;

}

.name{
  text-align: start;
  font-size: 50px;
  line-height: 45px;
  margin: 25px;
  margin-left: 0;
}

.button{
  text-decoration: none;
  color: #80ba65;
  border-radius: 15px;
  padding: 10px 35px 7px 35px;
  font-size: 25px;
  height: 42px;
  text-align: center;
  transition: all .3s;
  border: 1px solid #80ba65;
}

.button:hover{
  transform: scale(1.1);
  box-shadow: none;
  box-shadow: 0px 4px 10px rgba( 128,128,128, 0.7);
  background-color:#80ba65;
  color: white;
}

.quote{
  font-size: 30px;
  line-height: 25px;
  margin: 40px 25px 10px 0;
  width:450px;

}

.courses{
  display: flex;
  gap:50px;
  flex-direction: row;
  margin: 50px 0 0 0px;

}



.direction__title{
  font-size: 50px;
  line-height: 45px;
}

.direction{
  width: 100%;
  display: flex;
  justify-content: center;
  padding-top: 50px;
  align-items: center;
}

.direction__image{
  margin: 0;
  height: 100%;
}
.direction__text{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}

ul{
  display: flex;
  gap: 10px;
  flex-direction: column;
  list-style-type:none;
  padding-left: 2rem;
  font-size: 25px;
  justify-content: center;
  margin-left: 100px;
}

ul li {
  background-image: url(../src/images/Tick_2.svg);
  padding-left: 2rem;
  background-position: 0 0;
  background-size: 30px 30px;
  background-repeat: no-repeat;

}



.footer{
  width: 50%;
  margin: 100px auto 50px;
  display: flex;
  justify-content: center;
}

.footer__icon{
  width: 60px;
  height: 60px;
  transition: all .3s;
}

.footer__icon:hover {
  box-shadow: 0px 4px 10px rgba(187, 225, 250, 0.25);
  transform: scale(1.5);
}

.footer__links{
  display: flex;
  gap: 30px;
}


.block{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 50px;

}


.reviews{
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 50px;
}


.review__pic{
  transition: all .3s;
  width: 600px;
  max-height: 600px;
}

.review__pic:hover{
  box-shadow: 0px 4px 10px rgba(9, 10, 10, 0.25);
  transform: scale(1.1);
}


.arrow{
  transition: all .3s;
  width: 70px;
  margin: 70px;
}

.arrow:hover{
  transform: scale(1.1);
}

.block__title{
  font-size: 40px;
}







.diploms {
  margin-top: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 50px;
}

.diploma{
  display: flex;
  gap:10px;
}
.diploma__image{
  transition: all .3s;
  max-height: 190px;
}

.diploma__image:hover{
  box-shadow: 0px 4px 10px rgba(9, 10, 10, 0.25);
  transform: scale(1.1);
}


.arrow_diplom{
  transition: all .3s;
  width: 30px;
  margin: 30px;
}

.arrow_diplom:hover{
  transform: scale(1.1);
}

.login{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: auto 0;
}

.fieldset{
display: flex;
flex-direction: column;
border: none;
gap:30px;
}

.input{
  box-sizing: border-box;
  width: 250px;
  outline: none;
  font-size: 14px;
  line-height: 17px;
  padding-bottom: 13px;
  
}

.input::placeholder{
  color:rgba(196, 196, 196, 1);
}

.login__button{
  height: max-content;
}
.register__button{
  height: max-content;
}

.register{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: auto 0; 
}

.courses-page{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: auto 0; 
  padding-top: 50px;
  gap: 30px;
}
.card{
display: flex;
flex-direction: row;
width: 1000px;
border: 1px solid black;
border-radius: 15px;
padding: 10px;
background-color: #ffe5b4; 
justify-content: space-around;
}

.card__description{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: auto 0; 
}

.card__text{
  width: 400px;
}

.card__btn{
  height: max-content;
}

